  margin: 0;
  color: #c9d1d9;
  font-size: 1.25rem;
}
.modal-close {
  background: none;
  border: none;
  color: #8b949e;
  font-size: 2rem;
  line-height: 1;
  cursor: pointer;
  padding: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  transition: all 0.2s ease;
}
.modal-close:hover {
  background: #30363d;
  color: #c9d1d9;
}
