.form-group textarea,
.form-group select {
  width: 100%;
  background: #161b22;
  border: 1px solid #30363d;
  border-radius: 6px;
  padding: 0.75rem;
  color: #c9d1d9;
  font-size: 0.95rem;
  font-family: inherit;
  transition: border-color 0.2s ease;
}
.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
