}
.btn-primary,
.btn-secondary {
  padding: 0.75rem 1.5rem;
  border-radius: 6px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}
.btn-primary {
  background: #2ea043;
  color: white;
}
.btn-primary:hover {
  background: #2c974b;
}
.btn-secondary {
  background: transparent;
  color: #c9d1d9;
  border: 1px solid #30363d;
}
.btn-secondary:hover {
  background: #30363d;
}
@media (max-width: 768px) {
